.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.logo {
  color: #fff;
  font-weight: 700;
  transition: transform 0.3s ease;
}

.logo img {
  height: 65px;
  width: 160px;
}

@media (min-width: 767.98px) {
  .logo:hover {
    transform: scale(1.05);
  }
  .logo:focus {
    transform: scale(1.05);
  }
}

.main-title {
  font-family: "FuturaPT", sans-serif;
  font-weight: 700;
  font-size: 64px;
  color: #000;
}

.sub-title {
