}

.btn-link-transparent:focus {
  color: #fff;
  background-color: #000000;
}

.input {
  display: inline-block;
  height: 40px;
  padding-left: 20px;
  color: #000;
  background-color: transparent;
  border: 3px solid #000000;
  outline: none;
  font-size: 18px;
  font-weight: 400;
  text-transform: none;
  transition: all 0.3s ease;
}

.input::placeholder {
  font-family: "FuturaPT", sans-serif;
}

.input:hover {
  color: #fff;
  background-color: #000000;
}

.input:focus {
  color: #fff;
  background-color: #000000;
}

.copy {
  color: #484848;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
}

.header {
  position: relative;
  width: 100%;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 50;
  width: 100%;
  padding-top: 48px;
  transition: all 0.2s ease;
}

.nav::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: transparent;
  transition: all 0.2s ease;
}

.nav.nav--active {
  padding-top: 0px;
}

.nav.nav--active::before {
  background: #7c7c7c;
}

.nav__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  transition: all 0.2s ease;
}

.nav__logo {
  position: relative;
  z-index: 2;
}

.nav__form-wrapper {
  position: relative;
}

.nav__form {
  position: relative;
  z-index: 4;
  padding-right: 75px;
}

.nav__submit {
  position: absolute;
  top: 50%;
  left: 17px;
  width: 14px;
  height: 14px;
  transform: translateY(-50%);
}

.nav__submit svg {
  width: 14px;
  height: 14px;
  transition: transform 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__submit svg:hover {
    transform: scale(1.2);
  }
}

.nav__submit:hover .nav__search {
  border: 1px solid #f2f2f2;
}

.nav__search {
  width: 400px;
  height: 30px;
  padding-left: 42px;
  padding-right: 10px;
  outline: none;
  background: #ffffff;
  border-radius: 15.5px;
  border: 1px solid transparent;
  font-size: 18px;
  font-weight: 500;
  transition: border 0.2s ease;
}

@media (min-width: 767.98px) {
  .nav__search:hover {
    border: 1px solid #000;
  }
  .nav__search:focus {
    border: 1px solid #000;
  }
}

.nav__search.active {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.nav__icon-btns {
  display: flex;
  align-items: center;
}

.nav__cart-btn {
  position: relative;
  z-index: 7;
  display: flex;
  align-self: center;
  margin-right: 35px;
  outline: none;
  background: transparent;
  transition: all 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__cart-btn:focus svg {
    transform: scale(1.3);
  }
}

.nav__cart-btn svg {
  width: 25px;
  height: 25px;
  fill: white;
  transition: all 0.3s ease;
}

@media (min-width: 767.98px) {
  .nav__cart-btn svg:hover {
    transform: scale(1.1);
  }
}

.nav__cart-btn.active {
  padding: 5px;
  background-color: #fff;
  border: 1px solid black;
  border-radius: 100%;
}

.nav__cart-btn.active svg {
  fill: #000;
}

@media (max-width: 991.98px) {
  .nav__form {
    padding-right: 0;
  }
}

@media (max-width: 767.98px) {
  .nav {
    padding-top: 5px;
  }
  .nav.nav--active {
    padding-top: 5px;
  }
  .nav.nav--active::before {
    background: #7c7c7c;
  }
  .nav__form-wrapper {
    width: 100%;
  }
  .nav__body {
    flex-wrap: wrap;
    padding-bottom: 5px;
  }
  .nav__icon-btns {
    flex: 0 0 50%;
    display: flex;
    justify-content: flex-end;
  }
  .nav__logo {
    flex: 0 0 50%;
  }
  .nav__logo img {
    height: 45px;
    width: 120px;
  }
  .nav__form {
    width: 100%;
  }
  .nav__search {
    width: 100%;
  }
}

.cart {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 6;
  display: none;
  flex-direction: column;
  align-items: flex-end;
  height: 100vh;
  width: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
}

.cart.active-flex {
  display: flex;
}

.cart.active {
  transform: translateX(0px);
}

.cart.active .cart__close-field {
  opacity: 1;
}

.cart__close-field {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
  width: 100%;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  transition: opacity 0.3s ease 0.2s;
}

.cart__wrapper {
  position: relative;
  z-index: 6;
  overflow-y: auto;
  width: 25%;
  height: 100%;
  padding-top: 70px;
  background-color: #fff;
  transition: padding 0.2s ease;
}

.cart__wrapper.cart__wrapper--onscroll {
  padding-top: 20px;
}

.cart__quantity {
  padding: 0 30px;
  padding-bottom: 5px;
  font-weight: 700;
  font-size: 18px;
}

.cart__item {
  padding: 10px 0;
  padding-right: 24px;
  padding-left: 32px;
}

.cart__item:not(:last-child) {
  border-bottom: 1px solid black;
}

.cart__sum {
  padding: 0 27px;
  padding-top: 9px;
  padding-bottom: 17px;
  font-weight: 700;
  font-size: 18px;
  text-align: right;
}

.cart__btn-order {
  width: 100%;
  padding-top: 17px;
  padding-bottom: 16px;
}

@media (max-width: 4400px) and (min-width: 2000px) {
  .cart__btn-order {
    padding-left: 32px;
    text-align: left;
  }
  .cart__wrapper {
    width: 45%;
  }
  .cart__sum {
    text-align: left;
  }
}

@media (max-width: 3400px) and (min-width: 2000px) {
  .cart__btn-order {
    padding-left: 32px;
    text-align: left;
  }
  .cart__wrapper {
    width: 40%;
  }
  .cart__sum {
    text-align: left;
  }
}

@media (max-width: 2000px) {
  .cart__wrapper {
    width: 35%;
  }
}

@media (max-width: 991.98px) {
  .cart__wrapper {
    width: 100%;
  }
}

@media (max-width: 767.98px) {
  .cart__wrapper {
    padding-top: 20px;
  }
  .cart__wrapper.cart__wrapper--onscroll {
    padding-top: 20px;
  }
}

@media (max-width: 479.98px) {
  .cart__quantity {
    padding: 0 10px;
  }
  .cart__item {
    padding-right: 10px;
    padding-left: 10px;
  }
}

@media (max-height: 479.98px) and (orientation: landscape) {
  .cart__quantity {
    padding: 0 50px;
  }
  .cart__item {
    padding-right: 50px;
    padding-left: 50px;
  }
  .cart__sum {
    padding: 0;
  }
}

