.burger {
  position: relative;
  z-index: 5;
  display: block;
  width: 26px;
  height: 20px;
  outline: none;
  background: transparent;
  transition: transform 0.3s ease 0s;
}

.burger__upper-line, .burger__outer-line {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease 0s;
}

.burger__upper-line {
  top: 0;
}

.burger__middle-line {
  position: absolute;
  left: 0;
  top: 9px;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all 0.3s ease 0s;
}

.burger__outer-line {
  bottom: 0;
}

.burger.active .burger__middle-line {
  background-color: #000;
  transform: scale(0);
}

.burger.active .burger__upper-line {
  top: 9px;
  background-color: #000;
  transform: rotate(45deg);
}

.burger.active .burger__outer-line {
  bottom: 9px;
  background-color: #000;
  transform: rotate(-45deg);
}

@media (min-width: 767.98px) {
  .burger:hover {
    transform: scale(1.1);
  }
  .burger:focus {
    transform: scale(1.3);
  }
}

