// Файл обнуления стилей
@import "_nullstyle.scss";
//  //

// Mixins
@import "_mixins";
//  //

// Подключение шрифтов
@import "_fonts";

//  //

// Переменные
@import "_variables";
//  //

// Шаблоны
%scroll-del {
	&::-webkit-scrollbar {
		// chrome based
		width: 0px; // ширина scrollbar'a
		background: transparent; // опционально
	}
	-ms-overflow-style: none; // IE 10+
	scrollbar-width: none; // Firefox
}
// @extend %scroll-del
//  //

html {
	min-height: 100%;
	height: 100%;
}
body {
	position: relative;
	height: 100%;
	min-height: 100%;

	font-family: $ff, sans-serif;
	font-weight: 400;
}
.wrapper {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.lock {
	overflow: hidden;
}

.container {
	max-width: $maxWidthContainer + 30px;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
}

@media (max-width: $md2+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-width: $md3+px) and (orientation: portrait) {
	.container {
		padding-left: 10px;
		padding-right: 10px;
	}
}

@media (max-height: $md4+px) and (orientation: landscape) {
	.container {
		padding-left: 50px;
		padding-right: 50px;
	}
}
@import "_common";
@import "components/header.scss";
